*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
}

.App {
  text-align: center;
}

#myVideo{
  position: absolute;
  right: 0;
  bottom: 0;
  top:0;
  min-width: 100%; 
  min-height: 100%;
  z-index: 0;

}

.content {
  position: absolute;
    left: 30%;
    top: 30%;
    border: 2px solid white;
    display: inline !important;
    background: rgba(0, 0, 0, 0.5);
    color: #f1f1f1;
    display: flex;
    flex-grow: 2;
    height: 300px;
    padding: 100px;
    font-size: 28px;
    box-shadow: 0 0 20px rgba(192, 21, 21, 0.8);
}

@media screen and (max-width: 768px) {
   
  .content{
    left:0;
    padding-top: 105px;
    padding-right: 30px;
    padding-left: 22px;
    width: 100%;
   
 

  }

}